body{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color:#212529;
    text-align: left;

}
.first-row{
  display:flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  max-width: none;
}
.first-row-left{
    width: 43%;
    position: relative;
    float: left;
    padding-left: 15px;
    padding-right: 15px;
}
.first-row-left .h1{
    font-size:2.5rem;
    margin-bottom: 0.5rem;
    line-height: 1.2;
    color:#2d373c;
    word-wrap: break-word;
}
.first-row-left-text-container{
    padding: 3rem;
}
#hr{
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 0;
    border-top: 1px solid rbga(0,0,0,0.1);
}
.first-row hr{
    width: 150px;
    height: 10px;
    background: #32a0a0;
    margin-bottom: 3rem;
    margin-top: 3rem;
    margin-left: 0;
    border: 0;
    border-top: 1px solid rgba(0,0,0,0.1);
    overflow: visible;

}
#p{
    margin-top: 0;
    margin-bottom: 1rem;
    font-size:1rem;
    line-height: 1.5;
    display: block;

}
.first-row-right{
    width: 57%;
    height: 100vh;
    position: relative;
    float: right;
}
.img{
   width: 100%;
   height: 100%;
   background-image: url(/static/media/bkg-svg.b2227666.jpg);
   background-repeat: no-repeat;
   background-size: cover;
}
.big-font-2{
    font-size: 5rem;
    line-height: 0.8;
}
.big-font-1{
    font-size: 6.5rem;
    line-height: 0.8;
}
.text-light-grey{
    color:#939393;
}
.hr-m{
    width: 100px;
    height: 10px;
    background: #32a0a0;
}
.animated-container{
    display: block;
}
.animated-container .animated-content{
    transition: all 1.5s ease-in-out;
    transform: translate3d(20px, 0, 0);
    opacity: 0;
}
.animated-container .animated-content.active{
    transform: translate3d(0px, 0, 0);
    opacity: 1;
}
.timeline-badge{
    background-color: #32a0a0;
}
.no-gutters{
    margin-left:0;
    margin-right: 0;
}
.timeline-big-number{
    line-height: 0.7;
    font-size: 3.5em;
    font-weight: bold;
    color:#939393;
}
.border-lg-right{
    border-right: 1px solid #dee2e6;
}
.border-lg-left{
    border-left: 1px solid #dee2e6;
}
.d-inline-block{
    display: inline-block;
}
.border-badge-right{
    border-left: 10px solid #32a0a0;
    max-height: 55px;
}
.border-badge-left{
    border-left:10px solid #32a0a0;
    max-height: 55px;
}
.text-left{
    text-align: left;
}
.last-element{
    background-image: linear-gradient(to right, #50bebf, #46b7b8, #3bb0b1, #2fa9aa, #21a2a3, #189fa4, #0c9da6, #009aa7, #009ab1, #009aba, #009ac2, #0199ca);
    background-repeat: repeat-y;
    background-size: contain;
}
.hr-s{
    width: 50px;
    height: 10px;
    background:white;
}
.last-element span{
    color:white;
}
.orange-highlight{
    color:#ed821c;
}
a{
    text-decoration:none;
    background-color:transparent;
}

.orange-highlight:hover{
    color:#2387aa;
    text-decoration:underline;
}
